:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 500ms;
  --color-blue: #c6ebd1;
}

body {
  background-color: #f8f8f8;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  background-color: var(--color-blue);
  text-align: center;
  width: 100%;
  z-index: 999;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 5px 8px rgba(0, 0, 0, .5);
}

.gallery {
  max-width: 1140px;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-top: 80px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.photo-card {
  height: 200px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.photo-card img:hover {
  transform: scale(1.03);
}

.photo-card a {
  height: 100%;
  text-decoration: none;
  display: block;
}

.photo-card a:hover {
  cursor: zoom-in;
}

.info {
  width: 100%;
  background-color: #fefefe;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
  justify-content: space-around;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info-item {
  color: #1b5a2d;
  flex-direction: column;
  align-items: center;
  margin: 5px auto;
  display: flex;
}

.search-form button {
  cursor: pointer;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: var(--color-blue);
  border: none;
  border-radius: 2px;
  margin: 25px auto 30px;
  padding: 8px 20px;
  display: block;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .5);
}

